<template>
  <div v-if="isLoggedIn">
    <div class="image-container">
      <img v-for="img in allImages" :key="img.id" :src="img.link" alt />
    </div>
  </div>
  <h2 v-else>Log in to see your pictures!</h2>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ImageList",
  computed: mapGetters(["allImages", "isLoggedIn"]),
  methods: mapActions(["fetchImages"]),
  created() {
    this.fetchImages();
  },
};
</script>

<style scoped>
.image-container {
  column-count: 3;
  column-gap: 0;
}

img {
  max-width: 100%;
  padding: 5px;
}
</style>